    private homePlayClick(eventName: any, state: string) {
        console.log(state);
        switch (state) {
            case "playPlay":
                this.playPlay();
                break;
            case "playPause":
                this.playPause();
                break;
            case "playNext":
                this.playNext();
                break;
            case "playPrevious":
                this.playPrevious();
                break;
        }
    }

    private playPause() {
        if (this.canClickPlay) {
            console.log("playpuse");
            this.pause();
            this.PlayNeedBack = false;
            this.playNeedContinue = false;
        } else {
            this.ls.alert("", ",");
        }
    }

    private playPlay() {
        if (this.canClickPlay) {
            this.playNeedContinue = true;
            let flag = true;
            this.PlayQueue.forEach((queue, index) => {
                if (queue.state !== "stoped") flag = false;
            });
            if (flag) { //.
                this.releaseQueue();
                let family_id = this.ls.getFamilyId();
                let __this = this;
                this.loginService.GetBgJson(family_id).subscribe(bgData => {
                    bgData = bgData.sort(function () { Math.random() - 0.5 });
                    console.log(bgData);
                    for (let i = 0; i < bgData.length; i++) {
                        let name = bgData[i];
                        __this.addQueue({ type: "mp3", bgname: name, state: "stop" });
                    }
                    // bgData.forEach(bgName => {
                    //     __this.addQueue({ type: "mp3", bgname: bgName, state: "stop" });
                    // });
                    __this.play();
                });
            } else this.play();
        } else {
            this.ls.alert("", ",");
        }
    }

