    //
    releaseQueue() {
        this.PlayNeedBack = false;
        this.PlayQueue = [];
        if (this.CurrentPlay !== undefined) {
            this.CurrentPlay.release();
            this.CurrentPlay = undefined;
        }
        //aiuiPlugin.ttsStop();
    }
    //{"type":"tts/mp3","bgname":"mp3,","content":"tts","state":"stop/play/pause/stoped/back"}
    //
    private addQueue(playModel: any) {
        this.PlayQueue.push(playModel);
    }

    //,
    private insertQueue(playModel: any) {
        let flag = true;
        for (var i = 0; i < this.PlayQueue.length; i++) {
            var queue = this.PlayQueue[i];
            if (queue.state !== "stoped") {
                this.PlayQueue.splice(i + 1, 0, playModel);
                flag = false;
                break;
            }
        }
        if (flag) { //,
            this.addQueue(playModel);
        }
    }
    //
    private play() {
        let __this = this;
        console.log(this.PlayQueue);

        for (var i = 0; i < this.PlayQueue.length; i++) {
            var queue = this.PlayQueue[i];
            if (queue.state !== "stoped") {//,stoped
                __this.CurrentTTSIndex = i;
                if (queue.state === "pause")//,
                {
                    if (queue.type === "mp3" || queue.type === "smp3" || queue.type === "news") {
                        __this.CurrentPlay.play();
                    }
                    //} else if (queue.state === "stop") { //,
                } else { //.
                    if (__this.CurrentPlay !== undefined) { //
                        __this.CurrentPlay.release();
                        __this.CurrentPlay = undefined;
                    }
                    if (queue.type === "mp3") {
                        __this.downloadandPlayBg(queue.bgname);
                    } else if (queue.type === "smp3") {
                        __this.playBgMusic(queue.url);
                    } else if (queue.type === "news") {
                        __this.downloadPlayMp3(queue.url);
                    }
                }
                break;
            }
        }
    }
    private pause(): boolean {
        let __this = this;
        console.log("", __this.PlayQueue);
        // __this.PlayNeedBack = false;
        // if(__this.CurrentPlay !== undefined)
        //     __this.CurrentPlay.pause();
        //aiuiPlugin.ttsPause();

        for (var i = 0; i < __this.PlayQueue.length; i++) {
            var queue = __this.PlayQueue[i];
            if (queue.state !== "stoped") {//,stoped
                if (queue.state === "play") {
                    if (queue.type === "mp3" || queue.type === "smp3" || queue.type === "news") {
                        __this.CurrentPlay.pause();
                    }
                    //queue.state = "pause";
                    return true;
                } else {
                    //queue.state = "pause";
                    return false;
                }
            }
        }
        return false;
    }


    private playNext() {
        if (this.canClickPlay) {
            //1
            //if (this.CurrentTTSIndex < this.PlayQueue.length - 1) {
            //.
            this.playNeedContinue = true;
            this.clickNext = true;
            if (this.CurrentPlay != undefined)
                this.CurrentPlay.stop();
            // console.log("index:" + this.CurrentTTSIndex);
            // let queue = this.PlayQueue[this.CurrentTTSIndex];
            // queue.state = "stoped";
            else
                this.play();
            //}
        } else {
            this.ls.alert("", ",");
        }
    }

    private playPrevious() {
        if (this.canClickPlay) {
            this.playNeedContinue = true;
            this.clickPreview = true;
            if (this.CurrentPlay != undefined)
                this.CurrentPlay.stop();
            else {
                this.play();
            }
        } else {
            this.ls.alert("", ",");
        }
    }

    private playBgMusic(nativeUrl: string) {
        this.canClickPlay = true;
        this.CurrentPlay = this.audio.create(nativeUrl);
        this.CurrentPlay.onStatusUpdate.subscribe(status => {
            //2.,3..4
            console.log("index:" + this.CurrentTTSIndex + ",:" + status);
            if (status === 4) { //

                Emitter.fire("musicState", "stoped");
                //
                if (this.CurrentPlay !== undefined) {
                    this.CurrentPlay.release();
                }

                this.PlayQueue[this.CurrentTTSIndex].state = "stoped";

                if (this.PlayQueue[this.CurrentTTSIndex].type === "smp3") { //,
                    this.PlayQueue.splice(this.CurrentTTSIndex, 1);
                }

                if (this.clickNext) {
                    this.clickNext = false;

                    if (this.CurrentTTSIndex === this.PlayQueue.length - 1)
                        this.CurrentTTSIndex = 0;
                    this.play();
                } else if (this.clickPreview) {
                    this.clickPreview = false;

                    if (this.CurrentTTSIndex <= this.PlayQueue.length) {

                        this.PlayQueue[this.CurrentTTSIndex].state = "stop";

                        if (this.CurrentTTSIndex === 0)
                            this.CurrentTTSIndex = this.PlayQueue.length;

                        this.PlayQueue[this.CurrentTTSIndex - 1].state = "stop";
                        this.play();
                    }
                }
                else if (this.playSceneContinue) {
                    this.playSceneContinue = false;
                    this.play();
                }
                else if (this.playNeedContinue) {
                    this.play();
                }

            } else if (status === 2) {
                this.PlayQueue[this.CurrentTTSIndex].state = "play";
                Emitter.fire("musicState", "play");
            }
            else if (status === 3) {
                this.PlayQueue[this.CurrentTTSIndex].state = "pause";
                Emitter.fire("musicState", "pause");
            }
            console.log("onStatusUpdate:" + this.CurrentTTSIndex, this.PlayQueue);
        });
        this.CurrentPlay.play();
    }

