    private downloadPlayMp3(url: string) {
        let ft: FileTransferObject = this.txfr.create();
        let fn = this.file.dataDirectory + url.substring(url.lastIndexOf('/') + 1);
        ft.download(url, fn).then(
            (fe: FileEntry) => {
                this.playBgMusic(fe.nativeURL);
            },
            err => {
                console.log(JSON.stringify(err));
            }
        );
    }


    private download(url: string, scene: any) {
        let ft: FileTransferObject = this.txfr.create();
        let fn = this.file.dataDirectory + url.substring(url.lastIndexOf('/') + 1);
        ft.download(url, fn).then(
            (fe: FileEntry) => {
                scene.nativeURL = fe.nativeURL;
                this.ls.setScene(scene);
            },
            err => {
                console.log(JSON.stringify(err));
            }
        );
    }

    refstyle() {
        this.displayMethod = this.ls.getDisplayMethod();
        if (this.displayMethod === 0 || this.displayMethod === 1) {
            if (this.displayMethod === 0) {
                this.colora = "primary";
                this.colorb = "default";
            } else {
                this.colora = "default";
                this.colorb = "primary";
            }
        } else {
            this.displayMethod = 0;
            this.colora = "primary";
            this.colorb = "default";
        }
        this.ref.detectChanges();
    }
    registerBackButtonAction() {
        this.platform.registerBackButtonAction(() => {
            if (this.keyboard.isOpen()) {
                //
                this.keyboard.close();
                return;
            };
            //toastloadingOverlay
            // this.ionicApp._toastPortal.gaetActive() || this.ionicApp._loadingPortal.getActive() || this.ionicApp._overlayPortal.getActive()
            //this.ionicApp._toastPortal.getActive()2
            let activePortal = this.ionicApp._modalPortal.getActive() || this.ionicApp._overlayPortal.getActive();
            let loadingPortal = this.ionicApp._loadingPortal.getActive();
            if (activePortal) {
                //
                activePortal.dismiss().catch(() => {
                });
                activePortal.onDidDismiss(() => {
                });
                return;
            }
            if (loadingPortal) {
                //loading
                return;
            }
            let activeVC = this.nav.getActive();
            //  let page = activeVC.instance;


            let tabs = activeVC.instance.tabs;
            let activeNav = tabs.getSelected();
            return activeNav.canGoBack() ? activeNav.pop() : this.showExit();//this.showExit()
        }, 1);
    }

